@import '../common/styles/sass/variables.scss';
@import "../common/styles/sass/mixins";

.footer {
  height: 70px;
  border-top: 1px solid white;
  background-color: $bgColor;
  font-family: $titleFontFamily;

  .footerContainer {
    @include container;
    text-align: center;
    align-items: center;

    .copyrights {
      @include text($textFontFamily, 16px, $secondTextColor);
      font-weight: 400;


      span {
        color: $primaryColor;
      }
    }
  }
}



